import React, { useState } from "react";
import styled from "@emotion/styled";

import Figure from "../../../../../src/components/Figure";
import { color, riskOceanColor } from "../../../../../src/theme/color";
import { useInitialRender } from "../../../../../src/hooks";

const Styled = {
  Figure: styled(Figure)`
    position: relative;
    table-layout: fixed;
    width: 100%;
    max-width: none !important;
  `,
  Wrapper: styled.div`
    position: relative;
    width: 100%;
    margin-bottom: 0;
    box-shadow: 0 0 12px 4px ${color("shadowMedium")};
    overflow: hidden;
    border-top-left-radius: var(--img-border-radius);
    border-top-right-radius: var(--img-border-radius);
  `,
  Embed: styled.iframe`
    position: relative;
    width: 100%;
    min-height: 100%;
    min-width: 100%;
    background-color: ${riskOceanColor};
    border: none;
  `,
  Fallback: styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    pointer-events: none;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;

    /* Fade the fallback out once the iframe is loaded */
    &[data-loaded="true"] {
      opacity: 0;
    }
  `,
};

type ImageData = { previewBase64: string };

function getImageData(): ImageData {
  // We can't place static queries outside of components/,
  // so instead this is the base64 fallback for preview.png,
  // retrieved through a GraphQL query and then cached here.
  return {
    previewBase64: ``,
  };
}

export type DemoProps = {
  label: string;
  height: number;
  embedSrc: string;
  className?: string;
  style?: React.CSSProperties;
};

/**
 * Renders the risk game demo canvas as an iframe.
 * The inner page is a Vue app that renders the game canvas.
 */
export default function Demo({
  label,
  height,
  embedSrc,
  className,
  style,
}: DemoProps): React.ReactElement {
  const { previewBase64 } = getImageData();

  // Don't render the iframe until the component has mounted.
  // This prevents the iframe from loading before the app has finished
  // hydrating, which will cause the `onLoad` event to not fire.
  const initialRender = useInitialRender();
  const [isLoaded, setIsLoaded] = useState(false);

  return (
    <Styled.Figure
      style={{ width: "100%", maxWidth: "none", ...style }}
      className={className}
    >
      <Styled.Wrapper style={{ height: `${height}px` }}>
        {initialRender ? null : (
          <Styled.Embed
            src={embedSrc}
            onLoad={(): void => {
              setIsLoaded(true);
            }}
          />
        )}
        <Styled.Fallback
          data-loaded={isLoaded}
          style={{
            backgroundImage: `url('data:image/png;base64,${previewBase64}')`,
          }}
        />
      </Styled.Wrapper>
      <figcaption>{label}</figcaption>
    </Styled.Figure>
  );
}
